<template>
	<gracePage :customHeader="false">
		<view slot="gBody" class="grace-body">
			<view class="grace-form grace-margin-top">
				<view class="grace-items">
					<text class="dotnet-icons icon-wms-key icon grace-blue"></text>
					<view class="grace-label">旧密码</view>
					<input :type="showPassword ? 'text' : 'password'" class="input text-align-left" name="oldPassword" v-model="oldPassword" placeholder="请输入您的旧密码" />
					<view class="grace-form-funs grace-icons icon-eye" :style="{ color: showPassword ? '#007aff' : '' }" @tap="showPassword = !showPassword"></view>
				</view>

				<view class="grace-items">
					<text class="dotnet-icons icon-wms-key icon grace-blue"></text>
					<view class="grace-label">新密码</view>
					<input :type="showPassword ? 'text' : 'password'" class="input text-align-left" name="newPassword" v-model="newPassword" placeholder="请输入您的新密码" />
					<view class="grace-form-funs grace-icons icon-eye" :style="{ color: showPassword ? '#007aff' : '' }" @tap="showPassword = !showPassword"></view>
				</view>

				<view class="grace-items">
					<text class="dotnet-icons icon-wms-key icon grace-blue"></text>
					<view class="grace-label">再次输入新密码</view>
					<input
						:type="showPassword ? 'text' : 'password'"
						class="input text-align-left"
						name="repeatPassword"
						v-model="repeatPassword"
						placeholder="请再次输入您的新密码"
					/>
					<view class="grace-form-funs grace-icons icon-eye" :style="{ color: showPassword ? '#007aff' : '' }" @tap="showPassword = !showPassword"></view>
				</view>

				<view class="grace-margin-top"><button type="primary" class="grace-button" @click="changePassword">更改密码</button></view>
			</view>
		</view>
	</gracePage>
</template>

<script>
export default {
	data() {
		return {
			userIsLogin: false,
			oldPassword: '',
			newPassword: '',
			repeatPassword: '',
			showPassword: false
		};
	},
	onLoad: function() {
		var me = this;
		var userInfo = me.getCurrentUserInfo();
		if (userInfo === null) {
			//未登录，返回登陆界面
			uni.reLaunch({
				url: '../user/login'
			});
		}
	},
	methods: {
		changePassword() {
			var me = this;
			if (me.oldPassword.length < 6) {
				uni.showToast({
					icon: 'none',
					title: '当前密码最短为 6 个字符'
				});
				return;
			}
			if (me.newPassword.length < 6 || me.repeatPassword.length < 6) {
				uni.showToast({
					icon: 'none',
					title: '新密码最短为 6 个字符'
				});
				return;
			}
			if (me.newPassword != me.repeatPassword) {
				uni.showToast({
					icon: 'none',
					title: '新密码两次输入不一致'
				});
				return;
			}
			var userInfo = me.getCurrentUserInfo();
			uni.request({
				url: me.getServerUrl() + '/User/ChangePassword?oldPassword=' + me.oldPassword + '&newPassword=' + me.newPassword,
				method: 'POST',
				header: {
					appKey: me.appKey,
					appSecret: me.appSecret,
					userId: userInfo.Id,
					openId: userInfo.OpenId
				},
				success: res => {
					if (res.data.Status) {
						if (res.data.StatusCode == 'UserNotLogin') {
							uni.removeStorageSync('currentUserInfo');
							me.userIsLogin = false;
							uni.showToast({
								title: '用户已异地登录',
								duration: 2000,
								icon: 'none'
							});
						} else {
							//debugger;
							uni.showToast({
								title: '更改成功',
								duration: 2000,
								icon: 'success'
							});
							//getUserContact();
							var userInfo = res.data.Data;
							//向userInfo添加过期日期
							var cookieHours = me.getCookieHours();
							var timeNow = new Date();
							var expiredDate = timeNow.setTime(timeNow.setHours(timeNow.getHours() + cookieHours));
							userInfo.expiredDate = expiredDate;
							// 保存用户信息到全局的缓存中
							uni.setStorageSync('currentUserInfo', userInfo);

							setTimeout(function() {
								uni.reLaunch({
									url: 'usercenter'
								});
							}, 2000);
						}
					} else {
						uni.showToast({
							title: res.data.StatusMessage,
							duration: 2000,
							icon: 'none'
						});
					}
				}
			});
		},
		onNavigationBarButtonTap(e) {
			//console.log(e);
			if (e.type == 'back') {
				uni.reLaunch({
					url: 'usercenter'
				});
			}
		}
	}
};
</script>

<style></style>
